import React, { useState } from 'react'
import { CardBody, Form, FormGroup, Input, Label, Button } from 'reactstrap'
import { Spinner } from 'reactstrap'
import { encryptdata } from '../../utility/context/SecurityTool'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import crypto from 'crypto'
import Checkbox from '../../components/@vuexy/checkbox/CheckboxesVuexy'
import { Check } from 'react-feather'
import '../../assets/scss/pages/authentication.scss'
import axios from 'axios'
import './reg.css'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";

const Register = (props) => {
  const [next, setnext] = useState(false)
  const [accept, setaccept] = useState(false)
  const [disbaleRegister, setdisbaleRegister] = useState(false)
  const [successRegister, setsuccessRegister] = useState(false)
  const [errMsg, seterrMsg] = useState('')
  const [isLoading, setisLoading] = useState(false)
  const [name, setname] = useState()
  const [email, setemail] = useState()
  const [secretQustion1, setsecretQustion1] = useState()
  const [secretQustion2, setsecretQustion2] = useState()
  const [secretQustion3, setsecretQustion3] = useState()
  const [secretAnswer1, setsecretAnswer1] = useState()
  const [secretAnswer2, setsecretAnswer2] = useState()
  const [secretAnswer3, setsecretAnswer3] = useState()
  const [mobile, setmobile] = useState()
  const [password, setpassword] = useState()
  const [confirmPass, setconfirmPass] = useState()

  const callnext = (e) => {
    if (confirmPass === password) {
      setnext(!next)
    } else {
      seterrMsg('Passwords do not match')
      e.preventDefault()
    }
  }

  const hashPassword2 = async (password) => {
    //const salt = await bcrypt.genSalt(6);
    //const salt = await crypto.randomBytes(16).toString('hex');
    const salt = '0e09ca7a4644c68c6c002ed1338bb40b'
    //alert(salt);
    const hash = await crypto
      .pbkdf2Sync(password, salt, 1000, 20, `sha512`)
      .toString(`hex`)
    //const hashed = await bcrypt.hash(password, salt);
    //alert(hash);
    return hash
  }

  // const hashPassword = (password) => {
  //   return new Promise((accept, reject) => {
  //     const PASSWORD_LENGTH = 256
  //     const SALT_LENGTH = 64
  //     const ITERATIONS = 10000
  //     const DIGEST = 'sha256'
  //     const BYTE_TO_STRING_ENCODING = 'hex' // this could be base64, for instance
  //     var salt = crypto
  //       .randomBytes(SALT_LENGTH)
  //       .toString(BYTE_TO_STRING_ENCODING)

  //     //var salt = bcrypt.genSaltSync(10);
  //     //var hash = bcrypt.hashSync(password, salt);
  //     var hash = crypto.pbkdf2(
  //       password,
  //       salt,
  //       ITERATIONS,
  //       PASSWORD_LENGTH,
  //       DIGEST,
  //       (error, hash) => {
  //         if (error) {
  //           reject(error)
  //         } else {
  //           accept({
  //             salt,
  //             hash: hash.toString(BYTE_TO_STRING_ENCODING),
  //             iterations: ITERATIONS
  //           })
  //         }
  //       }
  //     )
  //     //var hash = crypto.pbkdf2(password, salt, iterations);
  //     return hash
  //   })
  // }

  const handleRegister = (e) => {
    setdisbaleRegister(true)
    setisLoading(true)
    e.preventDefault()
    hashPassword2(password).then((encpassword) => {
      e.preventDefault()
      var mykey1 = crypto.createCipher('aes-128-cbc', 'l@st@rz!')
      var mystr1 = mykey1.update(secretAnswer1, 'utf8', 'hex')
      mystr1 += mykey1.final('hex')

      var mykey2 = crypto.createCipher('aes-128-cbc', 'l@st@rz!')
      var mystr2 = mykey2.update(secretAnswer2, 'utf8', 'hex')
      mystr2 += mykey2.final('hex')

      var mykey3 = crypto.createCipher('aes-128-cbc', 'l@st@rz!')
      var mystr3 = mykey3.update(secretAnswer3, 'utf8', 'hex')
      mystr3 += mykey3.final('hex')
      const user = {
        email: email,
        name: encryptdata(name, false, encpassword),
        username: email,
        mobile: encryptdata(mobile, false, encpassword),
        password: encpassword,
        status: 'emailNotConfirmed',
        secrets: {
          secretQustion1,
          secretQustion2,
          secretQustion3,
          secretAnswer1: mystr1,
          secretAnswer2: mystr2,
          secretAnswer3: mystr3
        }
      }
      axios
        .post('/backendapi/saveuser', user)
        .then((res) => {
          setsuccessRegister(true)
          setisLoading(false)
        })
        .catch((e) => {
          seterrMsg('Something went wrong. Please try after sometime!!')
        })
    })
  }
  return (
    <>
      <p className="px-2 auth-title mb-0" style={{ color: 'red' }}>
        {errMsg}
      </p>
      {successRegister && (
        <p className="px-2 auth-title mb-0" style={{ color: 'green' }}>
          A confirmation email has been sent to your email. Please confirm your
          email. If already confirmed
        </p>
      )}
      <CardBody className="pt-1">
        {!next && (
          <div>
            <Form action="/" onSubmit={callnext}>
              <FormGroup className="form-label-group">
                <Input
                  type="text"
                  placeholder="Name"
                  required
                  value={name}
                  onChange={(e) => setname(e.target.value)}
                />
                <Label>Full Name</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="email"
                  placeholder="Email"
                  required
                  value={email}
                  onChange={(e) => setemail(e.target.value)}
                />
                <Label>Email</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="number"
                  placeholder="Phone Number"
                  required
                  value={mobile}
                  onChange={(e) => setmobile(e.target.value)}
                />
                <Label>Phone Number</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="password"
                  placeholder="Password"
                  required
                  value={password}
                  onChange={(e) => setpassword(e.target.value)}
                />
                <Label>Password</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="password"
                  placeholder="Confirm Password"
                  required
                  value={confirmPass}
                  onChange={(e) => setconfirmPass(e.target.value)}
                />
                <Label>Confirm Password</Label>
              </FormGroup>

              <div className="d-flex justify-content-center">
                <Button.Ripple color="warning" type="submit">
                  Next
                </Button.Ripple>
              </div>
            </Form>
          </div>
        )}
        {next && (
          <div>
            <Form action="/" onSubmit={handleRegister}>
              <FormGroup className="form-label-group">
                <Input
                  type="text"
                  placeholder="Secret Question 1"
                  required
                  value={secretQustion1}
                  onChange={(e) => setsecretQustion1(e.target.value)}
                />
                <Label>Secret Question 1</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="password"
                  placeholder="Secret Answer 1"
                  required
                  value={secretAnswer1}
                  onChange={(e) => setsecretAnswer1(e.target.value)}
                />
                <Label>Secret Answer 1</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="text"
                  placeholder="Secret Question 2"
                  required
                  value={secretQustion2}
                  onChange={(e) => setsecretQustion2(e.target.value)}
                />
                <Label>Secret Question 2</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="password"
                  placeholder="Secret Answer 2"
                  required
                  value={secretAnswer2}
                  onChange={(e) => setsecretAnswer2(e.target.value)}
                />
                <Label>Secret Answer 2</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="text"
                  placeholder="Secret Question 3"
                  required
                  value={secretQustion3}
                  onChange={(e) => setsecretQustion3(e.target.value)}
                />
                <Label>Secret Question 3</Label>
              </FormGroup>
              <FormGroup className="form-label-group">
                <Input
                  type="password"
                  placeholder="Secret Answer 3"
                  required
                  value={secretAnswer3}
                  onChange={(e) => setsecretAnswer3(e.target.value)}
                />
                <Label>Secret Answer 3</Label>
              </FormGroup>
              <FormGroup>
                <Checkbox
                  color="primary"
                  icon={<Check className="vx-icon" size={16} />}
                  label=" I accept the terms & conditions."
                  value={accept}
                  onChange={() => setaccept(!accept)}
                />
              </FormGroup>
              <div className="d-flex justify-content-between">
                <Button.Ripple
                  color="warning"
                  onClick={(e) => setnext(e.target.value)}
                >
                  Back
                </Button.Ripple>

                <Button.Ripple
                  color="warning"
                  type="submit"
                  disabled={disbaleRegister || !accept}
                >
                  {isLoading && <Spinner color="danger" size="sm" />}{' '}
                  {isLoading ? 'Registering...' : 'Register'}
                </Button.Ripple>
              </div>
            </Form>
          </div>
        )}
      </CardBody>
    </>
  )
}
export default Register
