import React, { useState, useEffect } from 'react'
import VerticalLayout from '../../layouts/VerticalLayout'
import FullLayout from '../../layouts/FullpageLayout'
import HorizontalLayout from '../../layouts/HorizontalLayout'
import themeConfig from '../../configs/themeConfig'
const layouts = {
  vertical: VerticalLayout,
  full: FullLayout,
  horizontal: HorizontalLayout
}
const ContextLayout = React.createContext()
const Layout = (props) => {
  const [activeLayout, setactiveLayout] = useState(themeConfig.layout)
  const [width, setwidth] = useState(window.innerWith)
  const [lastLayout, setlastLayout] = useState(null)
  const [direction, setdirection] = useState(themeConfig.direction)
  const updateWidth = () => setwidth(window.innerWidth)
  const handleWindowResize = () => {
    updateWidth()
    if (activeLayout === 'horizontal' && width <= 1199) {
      setactiveLayout('vertical')
      setlastLayout('horizontal')
    }
    if (lastLayout === 'horizontal' && width >= 1199) {
      setactiveLayout('horizontal')
      setlastLayout('vertical')
    }
  }
  useEffect(() => {
    if (window !== 'undefined') {
      window.addEventListener('resize', handleWindowResize)
    }
    handleDirUpdate()
    if (activeLayout === 'horizontal' && width <= 1199) {
      setactiveLayout('vertical')
    } else if (themeConfig.layout === 'horizontal' && width >= 1200) {
      setactiveLayout('horizontal')
    } else {
      setactiveLayout('vertical')
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const handleDirUpdate = () => {
    let dir = direction
    if (dir === 'rtl')
      document.getElementsByTagName('html')[0].setAttribute('dir', 'rtl')
    else document.getElementsByTagName('html')[0].setAttribute('dir', 'ltr')
  }

  const { children } = props
  return (
    <ContextLayout.Provider
      value={{
        state: { activeLayout, width, lastLayout, direction },
        fullLayout: layouts['full'],
        VerticalLayout: layouts['vertical'],
        horizontalLayout: layouts['horizontal'],
        switchLayout: (layout) => {
          setactiveLayout(layout)
        },
        switchDir: (dir) => {
          setdirection(dir)
        }
      }}
    >
      {children}
    </ContextLayout.Provider>
  )
}

export { Layout, ContextLayout }
