import axios from 'axios'
export default () => {
  axios.interceptors.response.use(
    function (response) {
      return response
    },
    function (error) {
      if (error?.response?.status === 401) {
        localStorage.clear()
        window.location.href = '/home'
      } else {
        return Promise.reject(error)
      }
    }
  )
}
